import OurFeatures from '@/components/OurFeatures'
export default {
  components: { MailForm, ProcessDev, SectionPortfolio, Prices, OurFeatures },
  props: ['portfolio'],
  data () {
    return {
      priceProducts: [
        { name: 'Корпоративный веб-сайт', price: '3 000', feature1: 'До 20 дней', feature2: 'Версия для планшетов', feature3: 'Мобильная версия', feature4: 'Кликабельный прототип' },
